@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
*,*:before,*:after{box-sizing:border-box}html,body,div,span,object,iframe,figure,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,code,em,img,small,strike,strong,sub,sup,tt,b,u,i,ol,ul,li,fieldset,form,label,table,caption,tbody,tfoot,thead,tr,th,td,main,canvas,embed,footer,header,nav,section,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline;text-rendering:optimizeLegibility;-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:none;-moz-text-size-adjust:none;text-size-adjust:none}footer,header,nav,section,main{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}input{-webkit-appearance:none;border-radius:0}.container-H{grid-area:H}.container-N{grid-area:N}.container-C{grid-area:C}.container{display:grid;grid-template:'H H' 'N C' / 15% 85%}*{transition:all .3s}html{font-size:62.5%}body{background-color:#edf1fb;font-family:'Roboto', sans-serif}.active-nav{background-color:rgba(215,175,255,0.342) !important;color:#fff !important}.active-user{color:#f38406}::-webkit-scrollbar{width:1rem}::-webkit-scrollbar-track{background:#faebeb}::-webkit-scrollbar-thumb{background:purple}::-webkit-scrollbar-thumb:hover{background:#038514}.header{display:flex;align-items:center;padding:1.5rem;justify-content:space-between;background-color:rgba(114,68,136,0.842)}.header__logo{width:20%}.header__logo img{width:11.5rem;-webkit-filter:brightness(0) invert(1);filter:brightness(0) invert(1)}.header__user{width:17%;display:flex;align-items:center;justify-content:space-evenly;cursor:pointer}.header__user h3{font-size:2.1rem;color:white}.header__user img{border-radius:50%;width:5.5rem;box-shadow:0 0.1rem 2rem rgba(93,1,93,0.582)}.navbar{background-color:#670585;display:flex;padding:3rem;height:auto;border-right:0.1rem solid rgba(128,9,128,0.342);flex-direction:column;justify-content:flex-start}.navbar a{text-decoration:none;cursor:pointer;padding:2rem;font-size:1.8rem;color:#f6f6f6;font-weight:500;border-bottom:0.1rem solid rgba(245,222,179,0.295)}.navbar a:hover{background-color:rgba(203,201,248,0.151);color:#fff}.content{display:flex;flex-direction:column}.user-profil-card__cover img{width:100%;height:33rem}.user-profil-card__info{display:flex;align-items:flex-end;justify-content:center;position:relative}.user-profil-card__info img{position:absolute;left:4%;top:-55%;box-shadow:0 0.3rem 3rem rgba(0,0,0,0.527);width:22rem;height:auto}.user-profil-card__info__desc{padding:2rem;font-size:2rem}.user-profil-card__info__desc h1{font-size:3rem;letter-spacing:.3rem;font-weight:bold;color:#250125;margin-bottom:2rem}.user-profil-card__info__desc p{margin:1rem 0;font-size:2.4rem;color:#333}.user-add-post{margin:2rem 0;width:85%;display:flex;flex-direction:column;align-items:flex-end}.user-add-post h1{width:95%;padding-top:3rem;border-top:0.1rem solid rgba(40,19,63,0.219);font-size:2rem;color:#2b0569}.user-add-post textarea{width:95%;height:10rem;padding:.5rem;font-size:2.5rem;margin:2rem 0;border-color:rebeccapurple;color:dimgray}.user-add-post textarea:focus{background-color:#f6f6f6;color:black;outline-color:purple}.user-add-post a{padding:1.2rem 3.5rem;cursor:pointer;color:white;border-radius:.7rem;font-size:1.8rem;background-color:#880c88}.user-add-post a:hover{background-color:#8f238f;transform:scale(1.01)}.user-share-posts{width:85%;padding:2rem 5rem;display:flex;align-items:center;margin:1rem 0}.user-share-posts:last-child{margin-bottom:10rem}.user-share-posts img{width:5rem;border-radius:50%;margin-right:1rem}.user-share-posts p{font-size:1.8rem;font-weight:300}.content-messages{display:flex;width:100%;height:auto}.content-messages__users{width:23%;padding:3rem;height:auto;border-right:0.1rem solid rgba(36,1,39,0.116);display:flex;align-items:center;flex-direction:column}.content-messages__users a{padding:1rem 0;width:70%;text-decoration:none;font-size:1.8rem;margin-top:1rem;text-align:left;list-style:circle}.content-messages__users a:not(:last-child){border-bottom:0.1rem solid rgba(32,3,41,0.157)}.content-messages__users a:hover{color:#05a127}.content-messages__message{width:77%;padding:3rem 0;display:flex;flex-direction:column;align-items:center}.content-messages__message__content{width:80%;display:flex;padding:4rem;box-shadow:0 0.1rem 2rem rgba(0,0,0,0.089);background-color:#f6e6fd;flex-direction:column;align-items:center;overflow-y:scroll;margin:2rem 0;height:50rem}.content-messages__message__content__group{width:80%;padding-bottom:2.2rem;margin:1rem 0;display:flex;align-items:center;padding:1rem}.content-messages__message__content__group:not(:last-child){border-bottom:0.1rem solid rgba(68,4,68,0.178)}.content-messages__message__content__group:hover{background-color:rgba(128,0,128,0.109)}.content-messages__message__content__group img{width:6rem;height:6rem;margin-right:1.5rem;border-radius:50%;box-shadow:0 0.1rem 2rem rgba(0,0,0,0.404)}.content-messages__message__content__group__answer{display:flex;flex-direction:column;justify-content:space-between;font-size:1.7rem}.content-messages__message__content__group__answer span{font-size:1.4rem;margin-top:1rem;color:#202}

